.timeContainer {
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
}

.slotOdd {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	background-color: #ffffff;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
	z-index: 1;
	width: 100%;
}

.slotEven {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	background-color: theme.palette.background.orange;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
	width: 100%;
}

.timeButton_Even {
	minWidth: 0;
	borderRadius: 0;
}

.timeButton_Even:hover {
	background-color: #faeb3e;
}

.timeButton_Odd {
	min-width: 0;
	border-radius: 0;
	background-color: rgba(0, 0, 0, 0.08);
}
.timeButton_Odd:hover {
	background-color: #faeb3e;
}

.weekText {
	text-align: center;
}
.narrowContainer {
	width: 80%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}
@media (max-width: 480px) {
	.narrowContainer {
		width: 100%;
	}
}

.calendar {
	width: 100%;
	border-radius: 4px;
	background-color: #fff3e0;
	border: 1px solid #f26924;
}

.bookingPage {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: center;
	flex: 1 1 auto;
	padding-bottom: 5%;
}

.bookingCalendar {
	width: 45%;
	max-width: 450px;
	position: relative;
}

.noServicesBookingLabel {
	text-align: center;
}