.healer{
    margin: 1em; 
	padding: 10px 10px 0 10px; /*padding-bottom is effectively done by .healerColumn2:after's height property*/
    background: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    height: 13.3em;
    box-shadow: -4px 5px 4px rgb(201, 201, 201);
	cursor: pointer;
	position: relative;
 transition: all 0.1s ease;
}

.healer:hover {
	background-color: #F59D78;
}

.healer:after {
	content: "";
	position: absolute;
	z-index: 1;
	bottom: 0;
	left: 0;
	pointer-events: none;
	width: 100%;
	height: 1.7em;
	background-image: 	linear-gradient(to bottom, 
						rgba(245, 157, 120, 0.1), 
						rgba(245, 157, 120, 1) 80%);
	transition: 0.05s;
	opacity: 0;
}

.healer:hover:after {
	opacity: 1;
}

.healerColumn2:after {
	content: "";
	position: absolute;
	z-index: 1;
	bottom: 0;
	left: 0;
	pointer-events: none;
	background-image: 	linear-gradient(to bottom, 
						rgba(255,255,255, 0.1), 
						rgba(255,255,255, 1) 80%);
	width: 100%;
	height: 1.7em;
	transition: 0.05s;
	opacity: 1;
}

.healer:hover .healerColumn2:after {
	opacity: 0;
}

.healerImgHovered:hover {
	background-color: #ffb596; /*Slightly lighter shade of orange*/
	
	
