    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.navbar-nav{
    background-color: white;
    width: 300px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    margin-left: auto;
    margin-right: auto;
}

.nav-item{
    width: calc(var(--nav-size)*5);
    display: flex;
    width: 300px;
    flex-wrap: wrap;
    margin-left: auto;
    align-items: center;
    justify-content: center;
}

.icon-button{
    margin-right: 10px;
    --button-size:calc(var(--nav-size)*.7);
    width: var(--button-size);
    height: var(--button-size);
    background-color: none;
    border-radius: 50%;
    padding: 5px;
    margin: 5px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: -webkit-filter 300ms;
    transition: filter 300ms;
    transition: filter 300ms, -webkit-filter 300ms;
}

.icon-button:hover{
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
}

.icon-button svg{
    fill: (--text-color);
    width: 20px;
    height: 20px;
}

