*{
    --bg: #242526;
    --bg-accent: #9D96B8;
    --text0color: #dadce1;
    --nav-size: 60px;
    --border: 1px solif #9D96B8;
    --border-radius: 8px;
    --speed: 500ms;
}
  
ul{
    list-style:none;
    margin: 0;
    padding: 0;
}

a{
    color: var(--text-color);
    text-decoration: none;
}
  
.navbar{
    margin-top: 10px;
    margin-bottom: 10px;
    line-height: 55px;
    height:40px;
    width: 100%;
    max-width: 100%;
    padding: 0 1rem;
    border-bottom: var(--border);
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.navbar-nav{
    background-color: white;
    width: 300px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    margin-left: auto;
    margin-right: auto;
}

.nav-item{
    width: calc(var(--nav-size)*5);
    display: flex;
    width: 300px;
    flex-wrap: wrap;
    margin-left: auto;
    align-items: center;
    justify-content: center;
}

.icon-button{
    margin-right: 10px;
    --button-size:calc(var(--nav-size)*.7);
    width: var(--button-size);
    height: var(--button-size);
    background-color: none;
    border-radius: 50%;
    padding: 5px;
    margin: 5px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: filter 300ms;
}

.icon-button:hover{
    filter: brightness(1.2);
}

.icon-button svg{
    fill: (--text-color);
    width: 20px;
    height: 20px;
}

.dropdown1{
    position: fixed;
    margin-top: 350px;
    transform: translateX(92px);
    background-color:#F59D78;
    width: 300px;
    height: 300px;
    flex-wrap: wrap;
    background-color:var(--bg);
    border:var(--border);
    border-radius: var(--border-radius);
    padding: 1rem;
    overflow-y: scroll;
}

.dropdown2{
    position: fixed;
    margin-top: 350px;
    transform: translateX(-28px);
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 300px;
    flex-wrap: wrap;
    background-color:var(--bg);
    border:var(--border);
    border-radius: var(--border-radius);
    padding: 1rem;
    overflow-y: scroll;
}

.dropdown3{
    position: fixed;
    margin-top: 350px;
    transform: translateX(-122px);
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 300px;
    flex-wrap: wrap;
    background-color:var(--bg);
    border:var(--border);
    border-radius: var(--border-radius);
    padding: 1rem;
    overflow-y: scroll;
}

.dd-menu {
	cursor: pointer;
}

.dd-menu p {
	-webkit-user-select: none; /* Safari */        
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* IE10+/Edge */
	user-select: none; /* Standard */
}

.dd-list {
	background-color: lightgrey;
	border: 2px solid lightgrey;
	padding: 3px;
	display: flex;
	flex-direction: column;
	max-height: 300px;
	overflow-y: auto;
	overflow-x: hidden;
	width: 100%;
	position: relative;
	z-index: 3;
}

.dd-list-item button {
	width: 100%;
	text-align: center;
	cursor: pointer;
}

.menu-option{
    margin-top: .4em;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: var(--border-radius);
    transition: background var(--speed);
    padding: 0.5rem;
	background-color: #9D96B8;
	line-height: normal;
	text-align: left;
}

.menu-option:hover{
    background-color: #F59D78;

}

.dropdownImg{
    height: 25px;
    width: 25px;
}

p{
    vertical-align: middle;
}

.arrowContainer {
	position: relative;
	justify-content: right;
	width: 100%;
	display: flex;
}

.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  position: absolute;
  right: 0;
  height: 10px;
  width: 10px;
  margin-right: 10px;
  margin-top: 5px;
  z-index: 1;
}

.arrow-right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.arrow-left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.arrow-up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
  top: 5px;
}

.arrow-down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}