.loaderContainer {
	position: fixed;
	left: 50%;
	top: 50%;
	transform: translate( -50%, -50% );
}

.loader {
	border: 16px solid #f3f3f3; /* Light grey */
	border-top: 16px solid #9D96B8; /* Purple */
	border-radius: 50%;
	width: 120px;
	height: 120px;
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% { transform: rotate(0deg); }
	100% { transform: rotate(360deg); }
}