.rdrNextPrevButton {
	background-color: #CEFA3E;
	min-width: 24px;
}
.rdrNextPrevButton:hover {
   background-color: #faeb3e;
}

.rdrCalendarWrapper {
	display: flex;
	flex-direction: column;
	width: 100%;
	min-width: 100%;
	max-width: 100%;
	margin: auto;
}

.rdrDateDisplayWrapper {
	width: 100%;
}

.rdrMonthAndYearWrapper {
	width: 100%;
	justify-content: center;
	align-items: center;
}

.rdrMonths {
	width: 100%;
}

.rdrMonthsVertical {
	width: 100%;
}

.rdrMonth {
	width: 100%;
}

.rdrWeekDays {}

.rdrWeekDay {
	color: black;
}

.rdrDays {}

.rdrDay {}

.rdrDayNumber {
	color: #B1EB3B;
}

.rdrDayNumber span {
	color: black;
}

.rdrMonthAndYearPickers {
	display: flex;
}

.rdrYearPicker {
	margin: 0;
	flex-grow: 2;
	min-width: 0;
}

.rdrYearPicker select {
	width: 100%;
}

.rdrMonthPicker {
	margin: 0;
	flex-grow: 4;
	min-width: 0;
}

.rdrMonthPicker select {
	width: 100%;
}