.singleHealer {
	z-index: 10 ;
	position: fixed;
	background-color: #8563a1;
	height: 700px;
	max-height: 95%;
	display: flex;
	width: 1300px;
	max-width: 95%;
	margin: 0 auto;
	border-radius: 30px;
}

.healerClicked {
	top: 0;
	z-index: 9;
	position: fixed;
	background: rgba(0, 0, 0, 0.5);
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.closeModal {
	height: 100%;
	width: 100%;
	cursor: pointer;
}

.healerModalButton {
	height: 100%;
	width: 100%;
	position: absolute;
	z-index: 1;
	left: 0;
	top: 0;
}

.healer .icon {
	position: relative;
	z-index: 2;
	-webkit-filter: brightness(100%);
	-webkit-transition: all 0.1s ease;
    -moz-transition: all 0.1s ease;
    -o-transition: all 0.1s ease;
    -ms-transition: all 0.1s ease;
    transition: all 0.1s ease;
}

.healer .icon:hover {
	-webkit-filter: brightness(70%);
    -webkit-transition: all 0.1s ease;
    -moz-transition: all 0.1s ease;
    -o-transition: all 0.1s ease;
    -ms-transition: all 0.1s ease;
    transition: all 0.1s ease;
}

.exitButton {
	float: right;
	position: relative;
	background: none;
	border: 0;
	cursor: pointer;
	overflow: hidden;
	width: 18px;
	height: 18px;
}

.exitButton::before,
.exitButton::after {
	background-color: black;
	content: '';
	display: block;
	height: 2px;
	left: 0;
	position: absolute;
	transform-origin: center left;
	/*transition: all 0.1s ease-in;*/
	width: 141.4214px;
	z-index: -1;
}

.exitButtonBack {
	width: 20px;
	height: 20px;
	margin-left: -19px;
	margin-top: -1px;
	cursor: initial;
}

.exitButtonBack::before,
.exitButtonBack::after {
	background-color: white;
	outline: 1px solid white;
}

.exitButton::before {
	top: -1px;
	transform: rotate(45deg);
}

.exitButton::after {
	bottom: -1px;
	transform: rotate(-45deg);
}

.exitButton:hover {
	color: #8A2387;
}
  
  
.exitButton:hover::before,
.exitButton:hover::after {
	background-color: grey;
}

.exitButtonBack:hover::before,
.exitButtonBack:hover::after {
	background-color: white;
}

.exitButtonBack:hover {
	color: white;
}

.exit {
    position: absolute;
    width: 100%;
	right: 0;
	margin-right: -1.8em;
	margin-top: -1.8em;
}

.reviewContainer {
	position: relative;
	width: 100%;
	background-color: lightgrey;
	margin: 1em 1em 1em 0em;
	display: flex;
	flex-direction: column;
	border-radius: 0 20px 20px 0;
}

.reviewHeaderContainer {
	padding: 10px;
	margin: 10px;
}

.reviewHeader {
	font-weight: initial;
	font-size: xx-large;
	font-family: inherit;
	position: initial;
	left: initial;
	padding-right: 1em;
	transform: initial;
	color: initial;
	outline: initial;
	text-shadow: initial;
	text-align: right;
	display: flex;
	flex-direction: row;
	align-content: space-between;
	padding-left: 20px;
	height: 1.2em;
	margin: 0em;
	justify-content: right;
	padding-right: 20px;
}

.reviewStars {
	align-items: center;
	font-size: 110%;
	height: 1em;
	color: gold;
	padding-right: 30px;
	text-align: right;
	-webkit-text-stroke: 0.5px black;
}

.reviewsContainer {
	display: flex;
	flex-direction: column;
	overflow-y: auto;
	/*border: 1px solid black;*/
}

.singleReview {
	border-radius:1%;
	object-fit: cover;
	margin: 0 20px;
	/*height: 150px;*/
	display: flex;
	flex-direction: column;
	box-shadow: 0px 4px 4px #b6b6b6;
	border: grey;
	border-width: 5px 20px 10px 5px;
	border-style: solid;
	background: inherit;
	margin-bottom: 10px;
}

.reviewText {
	margin: 8px 0 0 0;
	min-height: 50px;
	max-height: 130px;
	overflow-y: auto;
	border-top: 2px solid grey;
	padding-left: 5px;
	background: white;
}

.reviewText pre {
	margin: 0;
}

.healerSelected {
	display: inline-block;
	position: relative;
	margin: 1em 0 1em 1em;
	width: 100%;
	background: rgb(255, 255, 255);
	flex-direction: row;
	flex-wrap: wrap;
	border-radius: 20px 0 0 20px;
}

.img_healerSelected {
	border-radius:2%;
	object-fit: cover;
	width:13em;
	height:13em ;
	box-shadow: 3px 3px #000000;
}

.healerSelectedColumn1 {
	height: 12em;
	padding: 10px;
	margin: 10px;
}

.healerSelectedColumn2 {
	padding: 10px;
	margin: 10px;
}

.healerSelectedTop {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-content: space-between;
	padding-left: 20px;
	height: 1.2em;
	margin-top: 0em;
	font-size: xx-large;
}

.healerSelectedMiddle {
	align-items: center;
	font-size: 110%;
	/*max-height: 2em;*/
	color: grey;
	padding-left: 30px;
}

.healerSelectedBottom {
	display: table-cell;
	vertical-align: middle;
	padding-left: 20px;
}

.description {
	border-radius:1%;
	object-fit: cover;
	margin: 0 20px;
	padding: 0 5px;
	height: 50%;
	overflow-y: scroll;
	display: block;
	box-shadow: 0px 4px 4px #b6b6b6;
	border: lightgrey;
	border-width: 5px 5px 10px 20px;
	border-style: solid;
}

.smallPaddingRight {
	padding-right: 10px;
}

#noReviewText {
	display: flex;
	justify-content: right;
	padding-right: 50px;
	margin-right: 10px;
}

.bookerSelected {
	display: flex;
	position: relative;
	margin: 1em;
	padding: 1em;
	width: 100%;
	max-width: 100%;
	background: rgb(255, 255, 255);
	flex-flow: column;
	border-radius: 20px;
}

.bookingFormContainer {
	width: 45%;
	max-width: 450px;
	justify-content: center;
	position: relative;
}

.bookingPageContainer {
	display: grid;
	justify-content: center;
	align-items: center;
	grid-template-columns: 30% 30%;
	grid-row-gap: 5px;
	grid-column-gap: 20px;
	width: 100%;
	grid-auto-rows: 1fr;
}

.bookingServiceContainer {
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 2;
}

.bookingTimeContainer {
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 2;
	grid-row-end: 3;
}

.bookingSubmitButton {
	justify-content: center;
	align-items: center;
	display: flex;
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 3;
	grid-row-end: 4;
}

.reviewSelected {
	display: flex;
	position: relative;
	margin: 1em;
	padding: 1em;
	width: 100%;
	max-width: 100%;
	background: rgb(255, 255, 255);
	flex-flow: column;
	border-radius: 20px;
}

.reviewFormContainer {
	margin: auto;
	width: 100%;
	max-width: 400px;
	justify-content: center;
	position: relative;
}

.reviewPageContainer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.reviewRatingContainer {
	width: 100%;
}

.reviewDescriptionContainer {
	width: 100%;
	height: 100%;
}

#reviewDescription {
	height: 130px;
	overflow-y: scroll;
	overflow-x: hidden;
	width: 100%;
	margin-top: 6px;
}

.reviewSubmitButton {
	margin: 1em;
}

.bookerSelected hr, .reviewSelected hr {
	width: 100%;
}