.navbar-items {
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
    font-family: 'Poppins', sans-serif;
	position: relative;
}

.container {

}

.logo-container {
    width: 9vh;
    height: 9vh;
    justify-self: start;
    object-fit: fill;
    position: relative;
}

.logo {
	height: 100%;
	width: auto;
}

.nav-menu {
    display: flex;
    grid-template-columns: repeat(3,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 100%;
    justify-content:left;
	margin-left: 2vw;
}

.nav-links {
    color:black;
    text-decoration: none;
    padding: 0.5rem 1rem;
    z-index: 100;
}

.nav-links:hover {
    background-color: #E7E5E5;
    border-radius: 10px;
    transition: all 0.3s ease-out;
}

.sign-up-buttons {
    display: flex;
}

.sign-up-buttons button {
	margin-left: 0;
}

.sign-in-button button {
	background: initial;
}