.location{
    
}

.center_bar{
	color: #000000;
	border-color:#000000;
	height: 100%;
}

p1{
	font-size:1.5em;
	font-weight:400;
}

.sort{
    display: flex;
    flex-direction: row;
}

.sortBar{
	margin-left: 0;
	margin-right: auto;
	width: 100%;
	/*margin-top: -10em;*/
	height: 2.5em;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
