.sortBar .dd-menu {
	border: 1px solid black;
	height: 27px;
	text-align: center;
	background: #b4acd3;
	/*background: lightgrey;*/
	position: relative;
}

.sortBar pre {
	margin: 0;
}

.top_left{
	width: 100%;
	height: 100%;
}

.healerResults{
	position: relative;
	background-color: white;
	box-shadow: -1px 12px 20px rgb(255, 255, 255);
	
}

.users {
	overflow-y: scroll; 
	font-family: 'noto', serif;
	text-align: left;
	width: 100%;
	overflow: hidden;
}

.users div {
	border-radius: 0%;
}

.page-deets {
    width: 100%;
}

.page-deets h1 {
	margin-bottom: 0;
	font-weight: 500;
	font-size: 6em;
	font-family: 'Noto', sans-serif;
	position: absolute;
	left: 5%;
	top: -50px;
	color: white;
	outline: #000000;
	text-shadow:
	1px 1px 0 rgb(49, 49, 49);
	-webkit-user-select: none; /* Safari */        
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* IE10+/Edge */
	user-select: none; /* Standard */
}

.page-deets .img_region{
    overflow: hidden;
    border-radius:0.5%;
    object-fit: cover;
    width: 100%;
    height: 10em;
    margin: 0 0 0 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 5px 4px rgb(201, 201, 201);
}

.slidecontainer {
	width: 80%;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: auto;
	-webkit-user-select: none; /* Safari */        
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* IE10+/Edge */
	user-select: none; /* Standard */
}

.img_healer{
    border-radius:2%;
    object-fit: cover;
    width:11.3em;
    height:11.3em ;
    margin-left: auto;
    margin-right: auto;
    display: block;
    box-shadow: 3px 3px #000000;
}

.noMargin {
	margin: 0;
}

.healer{
    margin: 1em; 
	padding: 10px 10px 0 10px; /*padding-bottom is effectively done by .healerColumn2:after's height property*/
    background: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    height: 13.3em;
    box-shadow: -4px 5px 4px rgb(201, 201, 201);
	cursor: pointer;
	position: relative;
	-webkit-transition: all 0.1s ease;
    -moz-transition: all 0.1s ease;
    -o-transition: all 0.1s ease;
    -ms-transition: all 0.1s ease;
    transition: all 0.1s ease;
}

.healer:hover {
	background-color: #F59D78;
}

.healer:after {
	content: "";
	position: absolute;
	z-index: 1;
	bottom: 0;
	left: 0;
	pointer-events: none;
	width: 100%;
	height: 1.7em;
	background-image: 	linear-gradient(to bottom, 
						rgba(245, 157, 120, 0.1), 
						rgba(245, 157, 120, 1) 80%);
	transition: 0.05s;
	opacity: 0;
}

.healer:hover:after {
	opacity: 1;
}

.healerColumn2:after {
	content: "";
	position: absolute;
	z-index: 1;
	bottom: 0;
	left: 0;
	pointer-events: none;
	background-image: 	linear-gradient(to bottom, 
						rgba(255,255,255, 0.1), 
						rgba(255,255,255, 1) 80%);
	width: 100%;
	height: 1.7em;
	transition: 0.05s;
	opacity: 1;
}

.healer:hover .healerColumn2:after {
	opacity: 0;
}

.healerImgHovered:hover {
	background-color: #ffb596; /*Slightly lighter shade of orange*/
	
	
	/*Alternative options:*/
	/*background-color: initial;*/ /*This just reverts the background color to it's usual white color*/
	/*-webkit-filter: brightness(90%);*/ /*This darkens the div a bit, however this also affects child divs. 
	Can set the brightness over 100% to brighten the div instead*/
}

.healerColumn1 {
    height: 12em;
    padding: 10px;
    margin: 10px;
}

.healerColumn2 {
    /*padding: 10px 0 10px 0;*/
    /*margin: 10px 10px 0 10px;*/
    height: 100%;
	width: 100%;
	overflow-y: hidden;
	-webkit-transition: all 0.1s ease;
    -moz-transition: all 0.1s ease;
    -o-transition: all 0.1s ease;
    -ms-transition: all 0.1s ease;
    transition: all 0.1s ease;
}

.healerTop {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: space-between;
    margin: 0;
