.triangle {
	width: 100%;
	height: 100%;
	background-image:
	linear-gradient(to bottom right, #9D96B8 75%, transparent 0),
	linear-gradient(to bottom right, #9D96B8 25%, transparent 0);
	background-size: 50.05% 100%;
	background-repeat: no-repeat;
	background-position: left,right;
}

.loginForm{
	position:absolute;
	width:100vw;
	display:flex;
	flex-direction:column;
	flex-wrap:nowrap;
	margin:0px;
	overflow:auto;
	padding:0px;
	padding-top:0px;
	top:50%;
	left:50%;
	transform: translate(-50%, -50%);
	align-items:center;
	justify-content:center;
}

.topBlurb{
	font-size: 1.5em;
	margin: 1em;
	margin-left: 1.6em;
	text-align: center;
}

.middleBlurb{
	margin: 1em;
	display: flex;
	flex-direction:row;
}

.bottomBlurb{
    margin: 1em;
    display: flex;
    flex-direction:column;
}

