.signupFormContainer{
	position:relative;
	width:100vw;
	display:flex;
	flex-direction:column;
	flex-wrap:nowrap;
	margin:0px;
	overflow:auto;
	padding:0px;
	/*top:50%;
	left:50%;
	transform: translate(-50%, -50%);*/
	align-items:center;
	justify-content:center;
}

.signupForm{
	background-color: white;
	/*padding: 2vh 2vw;*/
	width: 400px;
	max-width: 100%;
	border-radius: 3px;
	overflow-y: scroll;
}

.signupPageContainer {
	width: 290px;
	max-width: 90%;
	height: 450px;
	padding: 23px 0;
	display: block;
	position: relative;
	border-radius: 3px;
	background-color: white;
	align-items: center;
	margin: 0 auto;
}

.signupCheckbox {
	width: 20px;
}

.nonTextSignupForm {
	position: relative;
	align-items: center;
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	padding-top: 23px;
	padding-bottom: 46px;
}

.nonTextSignupForm .multi-select {
	margin: 10px 0 10px 0;
	width: 100%;
	display: block;
}

.selectBox {
	padding: 0px 0 17px 0;
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
	justify-content: center;
}

.selectBox p {
	padding-top: 10px;
	padding-bottom: 5px;
}

#isHealer {
	width: initial;
	margin: 10px;
	right: 0;
}

.nonTextSignupForm .multi-select input {
	width: initial;
}

#signUpDescription {
	height: 100px;
	overflow-y: scroll;
	overflow-x: hidden;
	width: 100%;
	margin: 10px 0;
}

input, select {
	width: 100%;
	margin: 8px auto;
	padding: 5px;
	display: inline-block;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-sizing: border-box;
}

.signupButtonContainer{
	display: flex;
}

form p {
	margin-bottom: -10px;
	margin-top: 5px;
}

.nonTextSignupForm p {
	width: 100%;
}

.selectBox p {
	width: initial;
}

.warning {
  display: block !important;
  font-size: 0.75rem !important;
  margin-top: -0.5rem !important;
  margin-bottom: 0.5rem !important;
  color: #f14668 !important;
}

/*The two below stylings show the default state of the signup page*/
.signupPage{
	display:none;
}
#signupPage1{
	display:block;
}

.formField{
	text-align: center;
	width: 20em;
	height: 3.8em;
	background-color:#EAFFFD; 
	border-radius:0.4em;
}

.register{
	text-align: center;
	padding: 0.25em 0.5em;
	margin: 1em;
	border-radius: 0.2em;
}

.register:hover {
	transition: 0.3s;
	background-color: #EAFFFD;
	opacity:0.9;
}

.signin{
	text-align: center;
	padding: 0.25em 0.5em;
	margin: 1em;
	border-radius: 0.2em;
}

.signin:hover {
	transition: 0.3s;
	background-color: #EAFFFD;
	opacity:0.9;
}

.loginButton{
	text-align: center;
	border: 1px;
	border-color: black;
}

fieldset {
	border: 0;
	padding-left: 2em;
	padding-right: 2em;
}