.userProfile{
	position: fixed;
	background-color: #AB81CD;
	display: flex;
	height: 85%;
	width: 100%;
	margin: 0 auto;
}

.userContainer {
	display: inline-block;
	position: relative;
	margin: 1em 1em 1em 1em;
	width: 100%;
	background: rgb(255, 255, 255);
	flex-direction: row;
	flex-wrap: wrap;
	border-radius: 20px 0 0 20px;
}

.accountEditUnderText {
	align-items: center;
	font-size: 100%;
	height: 1em;
	padding-right: 30px;
	text-align: right;
}

.accountInfoEditorContainer {
	display: flex;
	flex-direction: column;
	overflow-y: auto;
}

.accountInfoEditorContainerContainer {
	width: 290px;
	max-width: 90%;
	padding: 23px 0;
	display: block;
	position: relative;
	border-radius: 3px;
	/*background-color: white;*/
	align-items: center;
	margin: 0 auto;
}

.accountInfoEditorContainerContainer p {
	margin-bottom: -10px;
	margin-top: 5px;
}

.accountInfoEditorForm {
	/*background-color: white;*/
	margin: 0 0.8vw;
	max-width: 100%;
	border-radius: 3px;
	overflow-y: scroll;
}

.accountInfoEditorForm::-webkit-scrollbar-track {
	margin: 1vh;
}

.accountInfoEditorForm::-webkit-scrollbar-thumb {
	border-radius: 5px;
}

.accountEditorHeaderContainer {
	padding: 10px 10px 10px 0;
	margin: 10px 20px 0 20px;
	border-bottom: 2px solid black;
}