.aboutText {
	font-family: 'Poppins', sans-serif;
	font-size: 1.15em;
	background-color: rgba(255, 255, 255, 1);
	padding: 3px 7px;
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 800px;
	max-width: 100%;
	overflow-y: auto;
	border: 3px solid rgba(60, 60, 60, 1);
	border-radius: 10px;
}

pre {
	/* Stolen from https://stackoverflow.com/questions/248011/how-do-i-wrap-text-in-a-pre-tag */
    white-space: pre-wrap;       /* Since CSS 2.1 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    word-wrap: break-word;       /* Internet Explorer 5.5+ */
}

.aboutContainer {
	display: flex;
	position: absolute;
	height: auto;
	width: 100%;
	height: 90%;
}

.privacyPolicy {
	height: 1000px;
	width: 800px;
	max-height: 90%;
	max-width: 100%;
	margin: auto;
	display: block;
}

.policyContainer {
	position: absolute;
	width: 100%;
	height: 90%;
	display: flex;
}