:root{
    --nav:#9D96B8;
}

.btn{
	margin-left: 1rem;
    margin-right: 1rem;
    width: 8.5rem;
    padding:10px 25px;
    border-radius: none;
    border: none;
    border-radius: 5px;
    cursor:pointer;
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
}

.btn:hover {
    transition: all 0.3s ease-out;
    background:#F59D78;
    transition: 200ms;
}

.btn--primary{
    background-color: #9D96B8;
    border-radius: 5px;
}

.btn--outline{
    background-color: none;
    color: #F59D78;
    padding: 10px 20px;
    border-radius: 2px;
    transition: all 0.3s ease-out;
}

.btn--bar{
    padding: 0.5rem 1rem;
    border-radius: 2px;
    font-size: 20px;
    color: black;
}

.btn--cards{
    border-radius: 2px;
    padding: 5px 10px;
    border-radius: 2px;
    font-size: 10px;
    color: black;
}

.btn--learn{
    margin-top: -150px;
}

.btn--login{
    margin-left: 1rem;
    margin-right: 1rem;
    width: 8rem;
    border: none;
    cursor:pointer;
    font-family: 'Poppins', sans-serif;
	background-color: blanchedalmond;
	padding: 0.4rem 1rem;
    border-radius: 2px;
    font-size: 20px;
    color: black;
	text-align: center;
	border: 1px;
	border-color: black;
}

.btn--login:hover{
	transition: all 0.3s ease-out;
    background:#FFDFB2;
    transition: 200ms;
}

.btn--changePage{
	text-align: center;
	padding: 0.25em 0.5em;
	margin: 1em;
	border-radius: 0.2em;
	width: 6rem;
	height: 3rem;
}

